body{
  background-color: black;
}
img {
  width: 1300px;
  height: 750px;
  margin: auto;
  background-size: cover;
}
.App {
  background-color: #022955d8;
  color: white;
  position: relative;
  bottom: 600px;
  border-radius: 10px;
  width: 1000px;
  height: 50px;
  position: relative;
  left: 150px;
  bottom: 730px;
  text-align: center;
  font-style: oblique;
  box-shadow: 5px 5px rgba(163, 217, 248, 0.5);
}
main {
  min-height: 500px;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.3));
  padding: 25px;
  width: 400px;
  position: relative;
  bottom: 700px;
  left: 400px;
}
.search {
  width: 100%;
  margin: 0 0 75px;
}
.search input{
  display: block;
  width: 300px;
  padding: 15px;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(64, 175, 250, 0.5);
  border-radius: 10px;
  margin: 30px;
  box-shadow: 5px 5px rgba(163, 217, 248, 0.507);
  color: #313131;
  font-size: 20px;
}
.search input :focus {
  background-color: rgba(255, 255, 255, 0.75);
}
.location-box {
  position: relative;
  bottom: 30px;
}
.location-box .location {
  color: #ffffff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
  margin: 10px auto;
}
.location-box .date {
  color: #ffffff;
  font-size: 22px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}
.weather-box {
  text-align: center;
}
.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.418);
  border-radius: 10px;
  padding: 15px 25px;
  color: #ffffff;
  font-size: 80px;
  font-weight: 800;
  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 6px 6px rgba(155, 153, 153, 0.2);
  bottom: 15px;
}
.weather-box .weather {
  color: #ffffff;
  font-size: 30px;
  font-weight: 600;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
  position: relative;
  bottom: 15px;
}